interface stat {
  name: string
  value: number
}

interface reviews {
  _id: string
  userId: string
  bestModel: string
}

export const processReviews = (reviews: reviews[]): stat[] => {
  const stats: stat[] = []
  reviews.forEach((review) => {
    const index = stats.findIndex((stat) => stat.name === review.bestModel)
    if (index === -1) {
      stats.push({ name: review.bestModel, value: 1 })
    } else {
      stats[index].value++
    }
  })
  return stats
}
